
/* ==========================================================================
   Susy Set Up
   ========================================================================== */
@import '../../node_modules/susy/sass/susy';

* {
    box-sizing: border-box;
}

$susy: (
    columns: 12,
    container: 100%,
    gutters: .25,
    global-box-sizing: border-box,
    debug: ( image: hide )
 );

$bp-sm: 30em;
$bp-md: 47em;
$bp-lg: 75em;

$breakpoint-no-query-fallbacks: true;

// Susy Layout & Debug
// ========================================================================== 

.susy-layout {
    position: relative;
    overflow: hidden;

    .debug {
        @include container();

        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 0;

        .col {
            @include span( 1 of 12 );

            height: 100vh;
            background: rgba( 0,  0,  0, .05 );
            overflow: hidden;

            &.last {
                @include span( 1  of 12 last );
            }
        }
    }

    .row {
        @include container();

        position: relative;
        z-index: 1;
        margin: gutter() 0;

        .col {
    
            line-height: 48px;
            text-align: center;

        }
    }
}

